.product {
  &_card {
    box-sizing: border-box;
    border-radius: 20px;
    border: 4px solid var(--on-element-color);
    overflow: hidden;

    padding: 8px;
    width: 100%;
    min-height: 300px;
  }

  &_info {
    display: flex;
    flex-direction: column;
    // justify-content: center;
    // align-items: center;
    gap: 8px;
    padding: 8px 20px;
  }

  &_photo {
    width: 100%;
    height: 120px;
    object-fit: cover;
    border-radius: 10px;
  }

  &_buy {
    display: flex;
    align-items: center;
    gap: 24px;

    &_btn {
      border-radius: 4px;
      text-align: center;
      text-decoration: none;
      border: none;
      background-color: var(--on-background-color);
      color: var(--background-color);
      font-weight: 800;
      padding: 0 12px;
      height: 24px;
      cursor: pointer;
      transition: (--background-color-animation);
    }
  }
}

@media (hover: hover) and (pointer: fine) {
  .product {
    &_buy {
      &_btn:hover {
        background-color: var(--second-on-color);
      }
    }
  }
}