@use '/src/style/setting' as s;

.products {
  display: flex;
  flex-direction: column;
  gap: 28px;
  
  &_list {
    display: flex;
    flex-direction: column;
    gap: 28px;

    box-sizing: border-box;
    padding: 12px 0;
  }

  &_container {
    display: grid;
    grid-template-columns: repeat(3, minmax(100px, 1fr));
    gap: 28px;
    width: 100%;
  }

  &_search {
    position: relative;
    display: flex;
    justify-content: space-around;
    align-items: center;

    margin-top: 40px;
    padding: 20px;
    border-radius: 20px;
    box-shadow: var(--default-box-shadow);
  }

  &_form {
    
  }
}

@media (max-width: s.$container-max-widths-lg) {
  .products {
    &_container {
      grid-template-columns: repeat(2, minmax(100px, 1fr));
    }
  }
}

@media (max-width: s.$container-max-widths-sm) {
  .products {
    &_container {
      grid-template-columns: repeat(1, minmax(100px, 1fr));
    }
  }
}